* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  color: #fff;
  background-color: #242333;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.8rem;
  display: flex;
}

.movie-container {
  margin: 2rem;
}

.movie-container select {
  color: beige;
  appearance: none;
  background-color: #444451;
  border: 0;
  border-radius: 5px;
  margin-left: 1rem;
  padding: .5rem 1.5rem;
  font-size: 1.4rem;
}

.seat {
  background-color: #444451;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  width: 1.5rem;
  height: 1.2rem;
  margin: .3rem;
}

.row {
  display: flex;
}

.selected, .selected1 {
  background-color: #0ff;
}

.occupied {
  background-color: #fff;
}

.gandhi {
  margin-bottom: .5rem;
}

.seat:nth-of-type(4) {
  margin-right: 1.5rem;
}

.seat:not(.occupied):hover {
  cursor: pointer;
  transform: scale(1.2);
}

.showcase.seat:not(.occupied):hover {
  cursor: default;
  transform: scale(1);
}

.showcase {
  color: #777;
  background-color: #0000001a;
  border-radius: .5rem;
  justify-content: space-between;
  padding: .5rem 1rem;
  list-style-type: none;
  display: flex;
}

.showcase li {
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
  display: flex;
}

.screen {
  background-color: #fff;
  width: 100%;
  height: 7rem;
  margin: 1.5rem 0;
  transform: rotateX(-45deg);
  box-shadow: 0 3px 5px #ffffff80;
}

.container {
  perspective: 1000px;
}

p span {
  color: #6feaf6;
}

